      <div class="blog">
      <p>慢慢在这个月又加了一些内容，中间经过误删仓库，在各种分支中绕来绕去，部署到github又白屏等各种错误：
        <ol>
          <li>Vue自带的vue.config.js文件配置与github是冲突的，需要在vue.config.js中设定
            <pre><code>
    module.exports = {
      publicPath: process.env.NODE_ENV === 'production'
        ? '/'
        : '/'
    }
            </code></pre>
          </li>
          <li>与其通过
            <pre><code>
    git add .
    git commit -m "Deploy dist files"
    git push -u origin gh-pages
            </code></pre>
            不如
            <pre><code>
    # 安装部署工具
    npm install --save-dev gh-pages

    # 在 package.json 中添加：
    {
      "scripts": {
        "predeploy": "npm run build",
        "deploy": "gh-pages -d dist"
      }
    }

    # 一键部署
    npm run deploy
            </code>
            </pre>
          </li>
        </ol>
      </p>
